import {
    CARE_PLAN_FOR_PATIENT,
    CONVO_PATIENT_ADMIN,
    FETCH_ERROR,
    FETCH_START,
    FETCH_SUCCESS,
    GET_ALL_PATIENTS,
    GET_CONVO_LIST,
    HIDE_MESSAGE,
    INIT_URL,
    PROD,
    SHOW_MESSAGE,
    SIGNOUT_USER_SUCCESS,
    SUGGESTIONS_PATIENT_ADMIN,
    USER_DATA,
    USER_TOKEN_SET
} from "../../constants/ActionTypes";
import axios from 'util/Api'
import { message } from "antd";


export const addPatient = values => async dispatch => {

    try {
        dispatch({ type: FETCH_START });

        const res = await axios.post(`${PROD}/patient/addpatient`, values)



        if (res) {

            dispatch({ type: FETCH_SUCCESS });
            message.success("Patient Added")
        }
    } catch (err) {
        const errors = err.response.data;
        message.error(errors);

        console.log("Error****:", errors);
    }
}



export const getPatients = values => async dispatch => {

    try {
        dispatch({ type: FETCH_START });

        const res = await axios.get(`${PROD}/patient/getallpatients`)



        if (res) {

            dispatch({ type: FETCH_SUCCESS });
            dispatch({ type: GET_ALL_PATIENTS, payload: res.data })
        }
    } catch (err) {
        const errors = err?.response?.data;
        message.error(errors);

        console.log("Error****:", errors);
    }
}


export const addToConvo = (id, name, conditions) => async dispatch => {

    try {
        dispatch({ type: FETCH_START });

        let allConditions = [];
        conditions.map((data, index) => (

            allConditions.push(data)


        ))
        const values = {
            "userId": id,
            "patientName": name,
            "Conditions": allConditions

        }

        const res = await axios.post(`${PROD}/patient/addtoconversation`, values)



        if (res) {

            dispatch({ type: FETCH_SUCCESS });
            dispatch(getConvoList())
            message.success("Patient Added")
        }
    } catch (err) {
        const errors = err?.response?.data;
        message.error(errors);

        console.log("Error****:", errors);
    }
}




