import { PlusCircleOutlined, PlusCircleTwoTone, SendOutlined } from '@ant-design/icons'
import { Button, Card, Col, Divider, Input, message, Popconfirm, Popover, Row, Select, Tag } from 'antd'
import Avatar from 'antd/lib/avatar/avatar'
import TextArea from 'antd/lib/input/TextArea'
import React, { useEffect, useState } from 'react'
import { shallowEqual, useDispatch, useSelector } from 'react-redux'
import { addConversation, addToConvo, getConversation, getConvoList, getPatients, getSuggestions, startingConversation, suggestionAdd } from '../../appRedux/actions'
import DisplayDate from '../../components/wall/DisplayDate'

function Messages() {

    const { patients } = useSelector(({ patient }) => patient)
    const { convoList } = useSelector(({ patient }) => patient)
    const { plan } = useSelector(({ patient }) => patient)
    const { convo } = useSelector(({ patient }) => patient)
    const { suggestions } = useSelector(({ patient }) => patient)

    const { Option } = Select;
    const Admin = JSON.parse(localStorage.getItem('User'))


    const [loadmoreFollow, setloadmoreFollow] = useState(3)
    const [loadmorerecommend, setloadmorerecommend] = useState(3)


    const [FromMessage, setFromMessage] = useState("admin")




    const [patientProfile, setPatientProfile] = useState();
    const [Plan, setPlan] = useState();

    const [Message, setMessage] = useState(String)

    const dispatch = useDispatch()

    useEffect(() => {
        dispatch(getPatients())
        dispatch(getConvoList())
    }, [])

    const confirm = (id, name, conditions) => {
        dispatch(addToConvo(id, name, conditions))

    }

    const messageFrom = (value) => {

        setFromMessage(value)
        if (value === "admin") {
            setMessage(`Hey ${patientProfile?.PatientName}, hope you are doing well\n`)

        }
        else {
            setMessage('')
        }

    }
    const setMoreFollow = () => {
        setloadmoreFollow(loadmoreFollow + 3)
    }
    const setLessFollow = () => {
        setloadmoreFollow(loadmoreFollow - 3)
    }


    const setMoreRecommend = () => {
        setloadmorerecommend(loadmorerecommend + 3)
    }

    const setLessRecommend = () => {
        setloadmorerecommend(loadmorerecommend - 3)
    }

    const addTextMessage = (data) => {


        setMessage(Message + data)
    }

    const inputChange = (e) => {
        setMessage(e)
    }

    const sendMessage = (adminId, patientId, messageFrom, msg) => {

        if (msg !== "") {
            dispatch(addConversation(adminId, patientId, messageFrom, msg))
            checkIfMintAdd()
        }
        else {
            message.error("please type a message and then send")
        }

        setMessage("")

    }

    const beginConversation = id => {
        dispatch(startingConversation(id))
        const selectedDirector = patients.find((element) => {
            return element._id.toString() === id;
        });
        setPatientProfile(selectedDirector);
        console.log("patient profile", selectedDirector)

        dispatch(getConversation(Admin.id, selectedDirector._id))
        dispatch(getSuggestions(Admin.id, selectedDirector._id))



        if (selectedDirector)
            setMessage(`Hey ${selectedDirector?.PatientName}, hope you are doing well\n`)


    }

    const [minuteCount, setminuteCount] = useState(0)
    // const [addMint, setaddMint] = useState(Boolean)
    // const [nextturn, setnextturn] = useState()

    const checkIfMintAdd = () => {
        let nextturn, mintueCount = 0, addMint;
        convo.map((data, index) => {

            if (data.messageFrom === "admin") {
                addMint = true
                nextturn = "patient"

            }
            if (data.messageFrom === "patient" && addMint === true && nextturn === "patient") {
                addMint = false
                nextturn = "admin"
                mintueCount++

            }
            // if (data.messageFrom === "admin" && nextturn === "admin") {

            //     mintueCount++
            //     
            // }
            // if (data.messageFrom === "patient" && nextturn === "patient") {

            //     mintueCount++
            //     
            // }


        })
        console.log("minute count", mintueCount)
        setminuteCount(mintueCount)
    }

    const addSuggestion = (val) => {
        dispatch(suggestionAdd(Admin?.id, patientProfile?._id, val))

    }

    useEffect(() => {
        if (plan) {
            console.log("effect plan ", plan)
            setPlan(plan)
        }
        else {
            setPlan(null)
        }


    }, [plan])

    useEffect(() => {
        if (convo) {
            checkIfMintAdd()
        }


    }, [convo])

    return (
        <div className="site-card-wrapper">
            <Row gutter={16}>
                <Col span={6} >
                    <Card title="Patient List" bordered={true} extra={
                        <Popover content={<>

                            {patients?.map((data, index) =>
                                <>

                                    <Button type="primary" ghost onClick={() => confirm(data.id, data.PatientName, data.Conditions)}>
                                        {data.PatientName}
                                    </Button>
                                    < br />
                                </>
                            )}
                        </>} title={<><br /><h4>Please select a patient</h4></>} >
                            <Button type="primary" shape="circle" icon={<PlusCircleTwoTone />} size={"large"} />
                        </Popover>

                    } >

                        <div  >
                            {convoList?.map((data, index) =>
                                <>
                                    {console.log(index)}

                                    <h2 >
                                        <Avatar
                                            className='gx-size-50 gx-mr-3'
                                            src={require('../../assets/images/placeholder.jpg')}
                                        />

                                        <Button size={"large"} style={{ color: "blue", borderColor: "blue" }} onClick={() => beginConversation(data.userId)} >
                                            {data.patientName}
                                        </Button>


                                        {/* <h4>Conditions :</h4>
                                        {data?.Conditions.map((data, index) =>
                                            <h5 > {data} </h5>
                                        )} */}


                                    </h2>



                                </>
                            )}
                        </div>
                    </Card>
                </Col>
                <Col span={12}>
                    <Card title={patientProfile ?
                        <>
                            <div style={{ overflow: "hidden" }}>
                                <h3 style={{ float: "left" }}>{patientProfile?.PatientName}</h3>
                                <h6 style={{ float: "right", color: "red" }}>

                                    {patientProfile?.Conditions.map((data, index) =>
                                        <>
                                            {data}
                                            <br />
                                        </>
                                    )}</h6>
                            </div>
                            {/* <div style={{overflow: "hidden">
                            <p style={{float: "left"}}>content1</p>
                            <p style={{float: "right"}}>content2</p>
                        </div> */}
                        </>
                        : "conversation"
                    }
                        bordered={true} >
                        <div style={{

                            width: "100%",
                            height: "200px",
                            overflowX: "hidden",
                            overflowY: "auto",

                        }}>
                            {convo && convo.map((data, index) => {

                                if (data?.messageFrom === "admin") {

                                    return <>
                                        <Tag color="blue" style={{ float: "right", maxWidth: "300px", wordBreak: "break-word", whiteSpace: "normal" }} >
                                            {/* {checkIfMintAdd(data?.messageFrom)} */}
                                            {data.message}
                                            <br />
                                            <DisplayDate date={data.createdAt} />
                                        </Tag>
                                        <br />
                                        <br />
                                        <br />


                                    </>
                                }
                                else {


                                    return <>

                                        <Tag color="default" style={{ float: "left", maxWidth: "300px", wordBreak: "break-word", whiteSpace: "normal" }} >
                                            {/* {checkIfMintAdd(data?.messageFrom)} */}

                                            {data.message}
                                            <br />
                                            <DisplayDate date={data.createdAt} />
                                        </Tag>
                                        <br />
                                        <br />
                                        <br />


                                    </>

                                }

                            }
                            )}
                        </div>

                        <Divider plain>Suggestions</Divider>

                        {Plan && <Tag color="red">Follow up suggestions</Tag>}
                        <br />
                        <div style={{

                            width: "100%",
                            height: "120px",
                            overflowX: "hidden",
                            overflowY: "auto",

                        }}>
                            {Plan && Plan.map(data =>
                                <>
                                    <Tag color="magenta">{data.planName}</Tag>

                                    <br />

                                    {data?.followUp?.slice(0, loadmoreFollow).map((data, index) =>
                                        <Tag color="green" onClick={() => addTextMessage(data)} style={{ maxWidth: "200px", wordBreak: "break-word", whiteSpace: "normal" }} >{data}</Tag>
                                    )}

                                    <br />
                                </>

                            )}
                            {plan && <Button type="primary" size="small" onClick={() => setMoreFollow()}>More</Button>}
                            {plan && <Button type="primary" size="small" onClick={() => setLessFollow()}>Less</Button>}


                            <br />

                            {Plan && <Tag color="red">Recommended Advices for patient</Tag>}
                            <br />

                            {Plan && Plan.slice(0, loadmorerecommend).map(data =>
                                <>
                                    <Tag color="magenta">

                                        {data.planName}
                                    </Tag>

                                    <br />
                                    {data?.recommended?.slice(0, loadmorerecommend).map((data, index) =>
                                        <Tag color="green" onClick={() => addTextMessage(data)} style={{ maxWidth: "200px", wordBreak: "break-word", whiteSpace: "normal" }}>

                                            {data}
                                        </Tag>

                                    )}
                                    <br />

                                </>

                            )}
                            {plan && <Button type="primary" size="small" onClick={() => setMoreRecommend()}>More</Button>}
                            {plan && <Button type="primary" size="small" onClick={() => setLessRecommend()}>More</Button>}
                        </div>
                        <Divider plain>Message</Divider>
                        <Row >
                            <Col span={20}>
                                <TextArea rows={6} placeholder="Type message here" onChange={(e) => inputChange(e.target.value)} bordered={false} value={

                                    patientProfile && Message


                                } />
                            </Col>
                            <Col span={4}>
                                <Button type="primary" shape="circle" icon={<SendOutlined />} onClick={() => sendMessage(Admin.id, patientProfile._id, FromMessage, Message)} style={{ float: "right" }} size={"large"} />
                                <Select

                                    style={{ width: 100 }}

                                    optionFilterProp="children"
                                    onChange={messageFrom}
                                    defaultValue={FromMessage}


                                >
                                    <Option value="admin">Admin</Option>
                                    <Option value="patient">Patient</Option>

                                </Select>
                            </Col>
                        </Row>
                    </Card>
                </Col>
                <Col span={6}>
                    <Card title="Patient Profile" bordered={true} >
                        {patientProfile &&
                            <>
                                <h3>
                                    NAME : {patientProfile?.PatientName}
                                </h3>
                                <h3>
                                    EMAIL : {patientProfile?.Email}
                                </h3>
                                <h3>
                                    Home Phone : {patientProfile?.HomePhone}
                                </h3>
                                <h3>
                                    Primary Insurance : {patientProfile?.PrimaryInsurance}
                                </h3>
                                <h3>
                                    social security# : {patientProfile?.SSN}
                                </h3>
                                <br />
                                <br />
                                <br />

                                <h2 style={{ color: "red" }}>Conditions</h2>
                                <h3 style={{ color: "red" }}>
                                    {patientProfile?.Conditions.map((data, index) =>
                                        <>
                                            {data}
                                            <br />
                                        </>
                                    )}
                                </h3>
                                {/* <h3><Button onClick={() => checkIfMintAdd()}>check minutes</Button></h3> */}
                                <br />
                                <h3>{minuteCount}{" "}{"minutes"}</h3>
                            </>
                        }
                    </Card>
                    <Card title="Custom Suggestions" bordered={true} >
                        <Input type="text" bordered={true} onPressEnter={(e) => addSuggestion(e.target.value)} />
                        <br />
                        <br />

                        {suggestions && suggestions.map((data, index) =>
                            <>
                                <Tag color="magenta" style={{ wordBreak: "break-word", whiteSpace: "normal" }} onClick={() => addTextMessage(data.Suggestion)}>

                                    {data.Suggestion}
                                </Tag>
                            </>
                        )}
                    </Card>
                </Col>
            </Row>
        </div >
    )
}

export default Messages
